* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter";
  font-style: normal;
  transition: all 0.3s ease;
}

html,
body {
  min-height: 100vh;
  background: var(--primary-bg);
  color: var(--primary-color);
}

