h1 {
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 31px;
  letter-spacing: -0.02em;
}

h2 {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 35px;
  letter-spacing: 0px;
}

h3 {
  font-family: "Inter";
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  letter-spacing: 0px;
}

p {
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0px;
  color: var(--primary-grey);
}

a {
  text-decoration: none;
  color: var(--primary-color);
}

input,
textarea {
  outline: none;
  border: none;
}

.main-content {
  margin-top: calc(var(--header-height));
  width: calc(100% - var(--sidebar-close-width));
  min-height: calc(100vh - var(--header-height));
  margin-left: var(--sidebar-close-width);
  transition: margin-left 0.3s ease;
}

.main-content.open {
  width: calc(100% - var(--sidebar-width));
  margin-left: var(--sidebar-width);
}

.section-header {
  padding: 1rem 1rem 0.3rem 3rem;
  border-bottom: var(--dark-grey) solid 3px;
  display: flex;
  justify-content: space-between;
}

.section-root {
  height: 100%;
}

.btn-light {
  background: #a3e3ff;
  color: #3772ff;
  border: none;
  border-radius: 10px;
  padding: 6px 12px;
  font-weight: 600;
  font-size: 14px;
}
.btn-light:disabled {
  opacity: 0.5;
}

.btn-dark {
  background: #3772ff;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 6px 12px;
  font-weight: 600;
  font-size: 16px;
}
.btn-dark:disabled {
  opacity: 0.5;
}

.filter-dark {
  filter: none;
}

.filter-white {
  filter: brightness(0%) saturate(100%) hue-rotate(0deg) grayscale(100%)
    contrast(1000%);
}
